<template>
  <div class="amap-container">
    <el-amap
      :vid="'amap-vue'" 
      :center="center"
      :zooms="zooms"
      :map-manager="amapManager"
      :animateEnable="false"
      :events="mapEvents"
    >
      <el-amap-marker
        v-for="(marker, index) in markInner"
        :key="marker.id"
        :position="marker.position"
        :content="'<div class=\'marker ' + marker.level + '\'><div style=\'display: none;\' class=\'id\'>'+ marker.id +'</div><div style=\'display: none;\' class=\'name\'>'+ marker.name +'</div><div style=\'display: none;\' class=\'level\'>'+ marker.level +'</div><div style=\'display: none;\' class=\'lat\'>'+ marker.latitudeGD +'</div><div style=\'display: none;\' class=\'lot\'>'+ marker.longitudeGD +'</div></div>'"
        :events="cityEvents"
      >
      </el-amap-marker>
      <el-amap-info-window
        :position="window.position"
        :content="window.content"
        :visible="window.visible"
        :events="window.events"
        :offset="[250, 110]"
      >
      </el-amap-info-window>
     </el-amap>
    <div class="title">
      智慧公证
      <div class="sj"></div>
    </div>
  </div>
</template>
<script>
import { AMapManager } from 'vue-amap'
import mapObj from '../map/getMap'
let amapManager = new AMapManager()
export default {
  name: 'amap',
  data () {
    return {
      center: [121.436816965957, 31.187615743114],
      zooms: [13, 18],
      toLaw: 14,
      amapManager: amapManager,
      markInner: [
        {
          name: '',
          id: '',
          leve: '',
          position: [31.117393, 121.456639]
        }
      ],
      window: {
        position: [700, 700],
        content: 'THIS IS MAP WINDOW',
        visible: false,
        events: {
          close: () => {
            this.window.visible = false
          }
        }
      },
      mapEvents: {
        /**
        * 地图加载完成后触发事件
        */
        complete: () => {
          let map = this.amapManager.getMap()
          mapObj.map = mapObj.getMap(map)
        }
      },
      cityEvents: {
        /**
         * marker点击事件
         */
        click: (e) => {
          let id = e.target.Qi.contentDom.querySelector('.id').innerHTML
          this.$store.dispatch('getIdPosition', id)
        }
      }
    }
  }
}
</script>
<style lang="scss" rel="stylesheet/scss">
  @import '../assets/scss/variables';
  @import '../assets/scss/mixins';
  .amap-container {
    position: relative;
    width: 100%;
    height: 100%;
    .title {
      position: absolute;
      bottom: 0;
      right: 0;
      padding-left: 20px;
      padding-right: 20px;
      padding-top: 15px;
      padding-bottom: 15px;
      height: 80px;
      background: $brand;
      color: $white;
      font-size: 30px;
      .sj {
        position: absolute;
        bottom: 0;
        left: -60px;
        width: 0;
        height: 0;
        border-bottom: 80px solid $brand;
        border-left: 60px solid transparent;
      }
    }
  }
  .marker {
    width: 19px;
    height: 33px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    cursor: pointer;
    &.City {
      width: 59px;
      height: 73px;
      background-image: url(../../static/images/City.png);
    }
    &.County {
      width: 39px;
      height: 53px;
      background-image: url(../../static/images/County.png);
    }
    &.Street {
      width: 39px;
      height: 53px;
      background-image: url(../../static/images/Street.png);
    }
    &.Community {
      width: 15px;
      height: 15px;
      border-radius: 50%;
      background: $brand-green;
    }
  }
  .stract {
    background-color: #348ac7;
    &:hover {
      background-color: #0fa3b1;
      opacity: 1;
    }
  }
  .dot {
    width: 20px;
    height: 31px;
    background-image: url(../assets/images/mark_bs.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    cursor: pointer;
  }
  .amap-info-content {
    padding: 0;
    border: 0;
    a {
      display: block;
      width: 100%;
      height: 50px;
      text-align: center;
      border-top: 1px solid $gray-lighter;
      line-height: 50px;
      color: $gray-light;
      font-size: 16px;
    }
  }
  .amap-info-close {
    margin-top: $module-margin;
    margin-right: $module-margin;
    font-size: 24px;
    color: $white;
    cursor: pointer;
    &:hover {
      color: $white;
    }
  }
  .amap-info-outer, .amap-menu-outer {
    border-radius: $border-radius-base;
    box-shadow: 0 0 20px 0 rgba(0,0,0,0.1);
    &:hover {
      box-shadow: 0 0 20px 0 rgba(0,0,0,0.1);
    }
  }
  .amap-info-sharp {
    position: absolute;
    left: -15px;
    bottom: 0;
    top: 0;
    margin: auto;
    background: none;
    width: 0;
    height: 0;
    border-top: 15px solid transparent;
    border-right: 15px solid $white;
    border-bottom: 15px solid transparent;
  }
  .info-bg {
    position: relative;
    width: 450px;
    height: 60px;
    padding-left: 25px;
    padding-right: 25px;
    text-align: left;
    line-height: 60px;
    font-size: 22px;
    color: $white;
    &.City {
      background: #f53837;
    }
    &.County {
      background: #f4d038;
    }
    &.Street {
      background: #ef671b;
    }
    &.Community {
      background: #70bb4d;
    }
    i {
      margin-right: 10px;
      font-size: 22px;
    }
  }
  .info-body {
    width: 100%;
    height: 190px;
    background-color: $white;
    .img-warp {
      position: relative;
      height: 100%;
      width: 150px;
      .img-bg {
        position: absolute;
        left: 0;
        right: 0;
        top: 15px;
        margin: auto;
        width: 130px;
        height: 130px;
      }
    }
    ul {
      float: right;
      width: 314px;
      height: 100%;
      margin: 0;
      padding: 10px 15px 10px 15px;
      list-style: none;
      li {
        width: 100%;
        height: 25%;
        span {
          width: 25%;
          height: 100%;
          box-sizing: border-box;
          float: left;
          color: $gray-light;
          font-size: 16px;
          display: block;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
        .inner {
          padding-left: 5px;
          width: 75%;
          float: right;
          color: $gray-dark;
        }
      }
    }
  }
  .router-warp {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .slide-left-enter, .slide-right-leave-active {
    opacity: 0;
    -webkit-transform: translate(50px, 0);
    transform: translate(100%, 0);
  }
  .slide-left-leave-active, .slide-right-enter {
    opacity: 0;
    -webkit-transform: translate(-50px, 0);
    transform: translate(-100%, 0);
  }
</style>



