import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/view/index'
import Aside from '@/components/aside'
import Asidepeople from '@/components/aside-people'
import Office from '@/components/office'
import Person from '@/components/person'
import officeDetail from '@/components/office-detail'

Vue.use(Router)

const routes = [
  {
    path: '/',
    redirect: '/index/aside'
  },
  {
    path: '/index',
    name: 'Index',
    component: Index,
    children: [
      {path: '/index/aside', component: Aside},
      {path: '/index/asidepeople', component: Asidepeople},
      {path: '/index/office', component: Office},
      {path: '/index/person', component: Person},
      {path: '/index/office-detail', component: officeDetail}
    ]
  }
]

export default new Router({
  linkActiveClass: 'active',
  routes
})



