const getters = {
  area: state => state.area,
  numberTitle: state => state.numberTitle,
  zoneList: state => state.zoneList,
  personnelList: state => state.personnelList,
  zoneDetail: state => state.zoneDetail,
  personnelDetail: state => state.personnelDetail,
  companyDetailList: state => state.companyDetailList
}
const actions = {
  getpresListShow ({commit}, iner) {
    commit('presListShow', iner)
  },
  getBack ({commit}, iner) {
    commit('back', iner)
  }
}
const mutations = {
  presListShow (state, iner) {
    if (state.personnelList[iner].swite === false) {
      state.personnelList.map((n) => {
        n.swite = false
      })
      state.personnelList[iner].swite = true
    } else {
      state.personnelList[iner].swite = false
    }
  },
  back (state, iner) {
    state.area = iner
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



