<template>
  <ul class="personnel-inner" v-iscroll>
    <div class="warp-pre">
      <li v-for="(item, index) in listArr">
        <a href="#/index/person">
          <div class="pic" style="background-image:url('static/images/ex_pic16.jpg');"></div>
          <span class="name">{{item.name}}</span>
          <br>
          <span class="number">{{item.number}}</span>
        </a>
      </li>
    </div>
  </ul>
</template>
<script>
export default {
  props: [
    'listArr'
  ],
  data () {
    return {
    }
  }
}
</script>
<style lang="scss" rel="stylesheet/scss">
  @import '../assets/scss/variables';
  @import '../assets/scss/mixins';
  .personnel-inner {
    width: 100%;
    height: 100%;
    overflow: hidden;
    margin: 0;
    padding: 0;
    list-style: none;
    cursor: pointer;
    .warp-pre {
      width: 100%;
      height: auto;
    }
    h3 {
      color: $white;
    }
    li {
      position: relative;
      width: 100%;
      height: auto;
      a {
        position: relative;
        width: 100%;
        height: auto;
        padding-top: 10px;
        padding-left: 100px;
        color: $white;
        display: block;
      }
      .pic {
        position: absolute;
        left: 10px;
        top: 10px;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background-size: cover;
      }
      span {
        display: inline-block;
        padding-bottom: 10px;
        font-size: 18px;
      }
    }
  }
</style>



